#navbar {
  background-color: #eeeeee;
  box-shadow: 4px 4px 10px 0 rgba(211, 211, 229, 0.3);
  display: flex;
  align-items: center;
  justify-content: center;
  background: hsl(180, 3%, 88%);
  overflow: hidden;
  border-radius: 15px;
}
nav ul {
  display: flex;
  text-align: center;
}
nav ul li {
  cursor: pointer;
  padding: 0 10px;
  transition: all 0.45s ease;
}
nav ul li a span {
  position: absolute;
  letter-spacing: 0.5px;
  transform: translateX(-70px);
  opacity: 0;
  pointer-events: none;
  transition: all 0.45s ease;
  font-size: 12px;
}
nav ul li:hover {
  background: rgb(206, 186, 186);
}
nav ul li a :hover {
  color: #fff;
  border-radius: "30px";
}
nav ul li:hover a span {
  transform: translateX(-55px);
  opacity: 1;
  pointer-events: visible;
}
nav ul li:hover a i {
  transform: translateX(-25px);
}
nav ul li a i {
  font-size: 20px;
  margin: 0 38 px;
  transition: 0.4s all;
}
